// CO Footprint Calculator (Frontend)
//  CO footprint  item  category  condition
//  backend/src/utils/co2Calculator.js

//  CO footprint  category (kg COe  1 item)
const CO2_BY_CATEGORY = {
  'Clothes & Fashion': 8.0, // , , 
  'Dorm Essentials': 10.0, // , , 
  'Books & Study': 20.0, // , , 
  'Kitchen & Appliances': 15.0, // , , 
  'Cleaning & Laundry': 6.0, // , , 
  'Hobbies & Entertainment': 10.0, // , , 
  'Sports Gear': 10.0, // , , 
  'Others': 5.0, //  
}

// Multiplier  condition
const CONDITION_MULTIPLIER = {
  'Like New': 0.9,
  'Good': 0.7,
  'Fair': 0.5,
}

/**
 *  CO footprint  item
 * @param {string} category - Category  item
 * @param {string} condition - Condition  item (Like New, Good, Fair)
 * @returns {number} CO footprint  kg COe
 */
export function calculateItemCO2(category, condition) {
  const baseCO2 = CO2_BY_CATEGORY[category] || CO2_BY_CATEGORY['Others']
  const multiplier = CONDITION_MULTIPLIER[condition] || CONDITION_MULTIPLIER['Good']
  return baseCO2 * multiplier
}

/**
 *  CO 
 * @param {number} co2Item1 - CO footprint  item 
 * @param {number} co2Item2 - CO footprint  item  (optional)
 * @returns {number} CO  kg COe
 */
export function calculateExchangeCO2Reduction(co2Item1, co2Item2 = null) {
  if (co2Item2 === null) {
    //  item   item  
    //  CO  75%  footprint
    return co2Item1 * 0.75
  }
  
  //  items 
  const averageCO2 = (co2Item1 + co2Item2) / 2
  const reductionFactor = 0.75 // 75% reduction
  return averageCO2 * reductionFactor
}

/**
 *  CO footprint  category
 * @param {string} category - Category  item
 * @returns {number} CO footprint  kg COe
 */
export function getCO2ByCategory(category) {
  return CO2_BY_CATEGORY[category] || CO2_BY_CATEGORY['Others']
}

/**
 *  CO footprint 
 * @returns {object} Object  category  CO footprint
 */
export function getAllCO2Footprints() {
  return CO2_BY_CATEGORY
}







